'use client';

import { Printer } from 'lucide-react';
import { Button } from '@/components/ui/button';
import {
    Dialog,
    DialogContent,
    DialogHeader,
    DialogTitle,
} from '@/components/ui/dialog';
import { format } from 'date-fns';
import { id as localeId } from 'date-fns/locale';

interface StrukData {
    kode_transaksi: string;
    created_at: string;
    nama_pelanggan: string | null;
    total_harga: number;
    total_bayar: number;
    kembalian: number;
    payment_method?: { nama_metode: string };
    user?: { name: string };
    details: {
        produk_resto?: { nama_produk: string };
        jumlah: number;
        harga_saat_transaksi: number;
        subtotal: number;
    }[];
}

interface RiwayatStrukDialogProps {
    isOpen: boolean;
    onOpenChange: (open: boolean) => void;
    data: StrukData | null;
}

export default function RiwayatStrukDialog({ isOpen, onOpenChange, data }: RiwayatStrukDialogProps) {
    if (!data) return null;

    const formatRupiah = (num: number) => 
        new Intl.NumberFormat('id-ID', { style: 'currency', currency: 'IDR', minimumFractionDigits: 0 }).format(Number(num));

    const formatDate = (dateString: string) => 
        format(new Date(dateString), 'dd MMMM yyyy, HH:mm', { locale: localeId });

    const handlePrint = () => {
        window.print();
    };

    return (
        <Dialog open={isOpen} onOpenChange={onOpenChange}>
            <DialogContent className="sm:max-w-sm bg-white p-0 overflow-hidden">
                <DialogHeader className="sr-only">
                    <DialogTitle>Struk Transaksi {data.kode_transaksi}</DialogTitle>
                </DialogHeader>

                <div id="struk-area" className="p-6 bg-white">
                    {/* Header Struk */}
                    <div className="text-center border-b border-dashed pb-4 mb-4">
                        <h2 className="text-xl font-bold uppercase tracking-wider">MainDiHatta</h2>
                        <p className="text-xs text-gray-500">Jl. Olahraga No. 1, Kota Anda</p>
                        <p className="text-xs text-gray-500 mt-1">{formatDate(data.created_at)}</p>
                    </div>

                    {/* Info Transaksi */}
                    <div className="text-xs text-gray-600 space-y-1 mb-4">
                        <div className="flex justify-between">
                            <span>Kode:</span>
                            <span className="font-mono">{data.kode_transaksi}</span>
                        </div>
                        <div className="flex justify-between">
                            <span>Kasir:</span>
                            <span>{data.user?.name}</span>
                        </div>
                        <div className="flex justify-between">
                            <span>Pelanggan:</span>
                            <span>{data.nama_pelanggan || 'Umum'}</span>
                        </div>
                    </div>

                    {/* List Item */}
                    <div className="border-t border-b border-dashed py-2 mb-4 space-y-2">
                        {data.details?.map((detail, idx) => (
                            <div key={idx} className="text-sm">
                                <div className="font-medium text-gray-900">{detail.produk_resto?.nama_produk}</div>
                                <div className="flex justify-between text-gray-500 text-xs">
                                    <span>{detail.jumlah} x {formatRupiah(detail.harga_saat_transaksi)}</span>
                                    <span>{formatRupiah(detail.subtotal)}</span>
                                </div>
                            </div>
                        ))}
                    </div>

                    {/* Total */}
                    <div className="space-y-1 text-sm">
                        <div className="flex justify-between font-bold text-lg text-gray-900">
                            <span>TOTAL</span>
                            <span>{formatRupiah(data.total_harga)}</span>
                        </div>
                        <div className="flex justify-between text-gray-600">
                            <span>Bayar ({data.payment_method?.nama_metode})</span>
                            <span>{formatRupiah(data.total_bayar)}</span>
                        </div>
                        <div className="flex justify-between text-green-600">
                            <span>Kembalian</span>
                            <span>{formatRupiah(data.kembalian)}</span>
                        </div>
                    </div>

                    {/* Footer */}
                    <div className="text-center mt-6 pt-4 border-t border-dashed">
                        <p className="text-xs text-gray-400">Terima kasih atas kunjungan Anda!</p>
                    </div>
                </div>

                {/* Tombol Print */}
                <div className="p-4 bg-gray-50 border-t flex justify-center print:hidden">
                    <Button className="w-full bg-gray-900 hover:bg-black" onClick={handlePrint}>
                        <Printer className="w-4 h-4 mr-2" /> Cetak Struk
                    </Button>
                </div>
            </DialogContent>
        </Dialog>
    );
}