'use client';

import { Search } from 'lucide-react';
import { Input } from '@/components/ui/input';

interface RiwayatFilterProps {
    searchQuery: string;
    setSearchQuery: (value: string) => void;
}

export default function RiwayatFilter({ searchQuery, setSearchQuery }: RiwayatFilterProps) {
    return (
        <div className="flex flex-col sm:flex-row justify-between items-start sm:items-center gap-4 print:hidden">
            <div>
                <h1 className="text-2xl font-bold text-gray-900">Riwayat Transaksi</h1>
                <p className="text-gray-500 text-sm">Daftar penjualan hari ini dan sebelumnya.</p>
            </div>
            <div className="relative w-full sm:w-64">
                <Search className="absolute left-3 top-2.5 h-4 w-4 text-gray-400" />
                <Input 
                    placeholder="Cari Kode / Nama Pelanggan..." 
                    className="pl-9"
                    value={searchQuery}
                    onChange={e => setSearchQuery(e.target.value)}
                />
            </div>
        </div>
    );
}