'use client';

import { useState, useEffect } from 'react';
import axios from '@/lib/axios';
import { toast } from 'sonner';

import RiwayatFilter from '@/components/karyawan/riwayat-transaksi/RiwayatFilter';
import RiwayatTable from '@/components/karyawan/riwayat-transaksi/RiwayatTable';
import RiwayatStrukDialog from '@/components/karyawan/riwayat-transaksi/RiwayatStrukDialog';

export default function HistoryPage() {
    const [transaksis, setTransaksis] = useState<any[]>([]);
    const [loading, setLoading] = useState(true);
    const [searchQuery, setSearchQuery] = useState('');
    
    const [selectedStruk, setSelectedStruk] = useState<any | null>(null);
    const [isDialogOpen, setIsDialogOpen] = useState(false);

    const fetchHistory = async () => {
        setLoading(true);
        try {
            const response = await axios.get('/api/transaksiRestos');
            const data = response.data.data ? response.data.data : response.data;
            
            setTransaksis(data);
        } catch (error) {
            console.error(error);
            toast.error("Gagal memuat riwayat transaksi.");
        } finally {
            setLoading(false);
        }
    };

    useEffect(() => { 
        fetchHistory(); 
    }, []);

    // --- 3. LOGIC (Filter) ---
    const filteredData = transaksis.filter(item => 
        item.kode_transaksi.toLowerCase().includes(searchQuery.toLowerCase()) ||
        (item.nama_pelanggan && item.nama_pelanggan.toLowerCase().includes(searchQuery.toLowerCase()))
    );

    const handleViewDetail = (transaksi: any) => {
        setSelectedStruk(transaksi);
        setIsDialogOpen(true);
    };

 
    return (
        <div className="space-y-6 pb-20">
            
        
            <RiwayatFilter 
                searchQuery={searchQuery}
                setSearchQuery={setSearchQuery}
            />

       
            <RiwayatTable 
                loading={loading}
                transaksis={filteredData}
                onViewDetail={handleViewDetail}
            />

 
            <RiwayatStrukDialog 
                isOpen={isDialogOpen}
                onOpenChange={setIsDialogOpen}
                data={selectedStruk}
            />

            <style jsx global>{`
                @media print {
                    /* Sembunyikan semua elemen body saat print */
                    body * { visibility: hidden; }
                    
                    /* Tampilkan hanya area struk dan isinya */
                    #struk-area, #struk-area * { visibility: visible; }
                    
                    /* Posisikan struk di pojok kiri atas kertas */
                    #struk-area {
                        position: absolute;
                        left: 0;
                        top: 0;
                        width: 100%;
                        padding: 0;
                        margin: 0;
                    }
                    
                    /* Hilangkan margin default browser */
                    @page { margin: 0; size: auto; }
                }
            `}</style>
        </div>
    );
}